.pincushion-tag-select__value-container {
  padding: 2px 4px !important;
}

.pincushion-tag-select__placeholder {
  margin-left: 8px !important;
}

.pincushion-tag-select__multi-value {
  color: #fff;
  text-shadow: 0 1px 0 rgba(0, 51, 83, 0.3);
  border-radius: 3px;
  background-color: #1b9dec;
  background-image: linear-gradient(to bottom, #1da7ee, #178ee9);
  background-repeat: repeat-x;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.2), inset 0 1px rgba(255, 255, 255, 0.03);
}

.pincushion-tag-select__multi-value__label {
  color: #fff !important;
  text-shadow: 0 1px 0 rgba(0, 51, 83, 0.3);
  padding: 2px 6px !important;
  font-size: 0.95em !important;
  border: 1px solid #0073bb;
  border-right: none;
  letter-spacing: 1px;
}

.pincushion-tag-select__multi-value__remove {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 0;
  padding: 0 !important;
  color: #fff;
  border: 1px solid #0073bb;
  border-width: 1px 1px 1px 0;
  border-radius: 0 3px 3px 0;
  cursor: pointer;
  overflow: hidden;
  transition: width 0.3s ease, padding 0.3s ease;
}

.pincushion-tag-select__multi-value:hover .pincushion-tag-select__multi-value__remove {
  width: 24px;
  padding: 0 4px 0 6px !important;
  border-left: 1px solid rgba(0, 0, 0, 0.1);
}

.pincushion-tag-select__multi-value__remove:hover {
  background-color: rgba(0, 0, 0, 0.1) !important;
  color: #fff !important;
}

/* Hide the slim indicator separator react-select inserts before the chevron */
.pincushion-tag-select__indicator-separator {
  display: none;
}

/* Make the real text input fully transparent so no extra block shows through */
.pincushion-tag-select__input-container {
  margin: 0;
  padding: 0;
}

.pincushion-tag-select__input input {
  background: transparent !important;
  border: none !important;
  box-shadow: none !important;
  color: inherit;
  padding: 0;
  min-width: 0;
}

/* Dropdown Menu List Styling */
.pincushion-tag-select__menu-list {
  /* Add some padding or other base styles if needed */
}

/* Individual Option Styling */
.pincushion-tag-select__option {
  /* Alternating row background */
  &:nth-child(even) {
    background-color: #f5fafd;
  }

  /* Focused/Active Option Styling */
  /* Note: react-select manages hover and focus states */
  &--is-focused {
    background-color: #edf2f9;
    /* Ensure text color remains readable, override potential defaults */
    color: inherit;
  }
}

/* Highlighted substring style */
.pincushion-tag-select__option strong {
  /* Make the highlight distinct but not jarring */
  font-weight: bold;
  /* You could add color or background-color here if desired */
  /* color: #0056b3; */
}
.pincushion-tag-select__control input[type="text"],
.pincushion-tag-select__control input[type="url"],
.pincushion-tag-select__control textarea {
  box-shadow: none !important;
  border: none !important;
  max-width: none;
  padding: 0;
  background: transparent !important;
}
